@import "./../../colors.scss";

#home-description-container {
  box-sizing: border-box;
  padding: 10rem 1rem;
  height: 100vh;
  background: rgb(43,45,66);
  background: linear-gradient(0deg, rgba(43,45,66,1) 55%, rgba(141,153,174,1) 81%);
  display: flex;
  justify-content: center;
  align-items: center;
}

#home-aboutme-container {
  box-sizing: border-box;
  padding: 10rem 13rem;
  width: 100%;
  display: flex;
  justify-content: space-around;
}

#home-techno-container {
  box-sizing: border-box;
  padding: 3rem 5rem;
  width: 100%;
  background-color: $White;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

#techno-heading {
  color: $MaxBlue;
}

#home-project-container {
  box-sizing: border-box;
  padding: 3rem 5rem;
  background-color: $MaxBlue;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#projects-heading {
  color: $White;
}

#footer-container {
  box-sizing: border-box;
  padding: 3rem 5rem;
  height: 30vh;
  display: flex;
  align-items: center;
}

@media screen and (max-width: 480px) {
  #home-description-container {
    padding: 2rem;
    flex-direction: column;
    align-content: center;
  }

  #home-page-heading {
    font-size: 6rem;
    margin: 2rem 0;
  }

  #home-aboutme-container {
    padding: 2rem;
  }

}
